
.App {
    overflow:hidden;
}

.scroll-content {
    height: 100svh;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scroll-snap-stop: always;
    overflow-y: scroll;
}


.scroll-content>* {
    scroll-snap-align: start;
}

::-webkit-scrollbar {
    width: 0;
  }

